export default {
  name: 'Sidebar',
  computed: {
    auth() {
      return this.$store.getters.auth
    },
    urls() {
      let urls = [
        {
          text: 'Home',
          to: '/home',
          icon: 'home',
        },
        {
          text: 'Artikel',
          to: '/articles',
          icon: 'book',
        },
        {
          text: 'Video',
          to: '/videos',
          icon: 'film',
        },
        {
          text: 'Kategori',
          to: '/categories',
          icon: 'database',
        },
        {
          text: 'Minitutor',
          to: '/minitutors',
          icon: 'users',
        },
        null

      ];

      if(this.auth) {

        if(!this.auth.minitutor) {
          urls.push({
            text: 'Jadi MiniTutor',
            to: '/join-minitutor',
            icon: 'user-plus',
          })
        }

        urls.push({
          text: 'Dasbor kamu',
          to: '/dashboard/me',
          icon: 'user',
        })

        if(this.auth.minitutor && this.auth.minitutor.active) {
          urls.push({
            text: 'Dasbor MiniTutor',
            to: '/dashboard/minitutor',
            icon: 'clipboard',
          })
        }

      } else {
        urls = [
          ...urls,
          {
            text: 'Masuk',
            to: '/login',
            icon: '🎭',
          },
          {
            text: 'Daftar',
            to: '/register',
            icon: '🎭',
          },
        ]
      }
      return urls;
    }
  },
  methods: {
    logout(e) {
      e.preventDefault();
      this.$toast.confirm.danger(
        async () => {
          try {
            await this.$store.dispatch('auth/logout')
            window.location.reload()
          } catch (e) {
            this.$toast.danger('Logout gagal!')
          }
        },
        null,
        { message: 'Anda yakin ingin keluar?' }
      )
    },
    onclick() {
      this.$sidebar.display(false)
    }
  },
}
