import algoliasearch from 'algoliasearch/lite'
import vClickOutside from 'v-click-outside'

export default {
  name: "SearchModal",
  directives: {
    clickOutside: vClickOutside.directive
  },
    data() {
    return {
      focus: false,
      input: '',
      loading: false,
      results: [],
      queryResult: '',
      typing: false,
      typingTimeout: null
    }
  },
  computed: {
    empty() {
      return !this.input
    },
    logging() {
      if(this.typing) {
        return 'Sedang mengetik...'
      }
      if(this.loading) {
        return 'Sedang mencari...'
      }
      return ''
    },
    searchResults() {
      if(this.input > 1) {
        return this.results
      }
      return []
    }
  },
  watch: {
    $route() {
      this.$emit('close')
    },
    input(val) {
      if(val.length > 1) {
        this.typing = true
        if (this.typingTimeout) {
          clearTimeout(this.typingTimeout)
        }
        this.typingTimeout = setTimeout(() => {
          this.typing = false
        }, 1000)
      }
    },
    typing(val) {
      if(!val && this.input.length > 1) {
        this.search(this.input)
      }
    }
  },
  mounted() {
    this.$refs.input.focus()
  },
  methods: {
    onClickOutside() {
      this.$emit('close')
    },
    reset() {
      this.input = ''
      this.results = []
      this.queryResult = ''
    },
    async search(input) {
      this.loading = true
      const client = algoliasearch(this.$nuxt.$config.algoliaAppId, this.$nuxt.$config.algoliaApiKey);
      try {
        const res = await client.initIndex(this.$nuxt.$config.algoliaIndexName).search(input, { highlightPreTag: '<span class="bg-yellow-200">', highlightPostTag: '</span>' })
        this.results = res.hits || []
        this.queryResult = res.query
      } catch (e) {
        // console.log(e)
      }
      this.loading = false
    }
  },
}
